                th Aspecto legal
                th Descripcin y fundamento
                th Soporte Documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Aspecto legal") #[b Objeto de la Resolucin]
                td(data-label="Descripcin y fundamento") Adoptar el manual que regula los procedimientos durante todas las etapas del transporte, manejo y movilizacin de animales vivos.
                td(data-label="Soporte Documental (texto exacto)") #[b Resolucin 20223040006915 (Art. 1):] "El objeto de la presente resolucin es adoptar el manual de procedimiento para el transporte, manejo y movilizacin de animales en pie, durante todas las etapas, contenido en el anexo de la presente resolucin el cual hace parte integral de la misma".
              tr(data-aos="fade-right")
                td(data-label="Aspecto legal") #[b mbito de aplicacin]
                td(data-label="Descripcin y fundamento") Aplica a todas las personas naturales y jurdicas que movilizan o transportan animales por va terrestre y fluvial, incluyendo las principales especies de produccin.
                td(data-label="Soporte Documental (texto exacto)") #[b Resolucin 20223040006915 (Art. 2):] "Aplica a las personas naturales y jurdicas involucradas en el transporte, manejo y movilizacin por va terrestre y fluvial de animales de las especies bovina, porcina, caprina, ovina, aviar, bufalina, equina y las dems especies de consumo humano".
              tr(data-aos="fade-right")
                td(data-label="Aspecto legal") #[b Finalidad (bienestar animal)]
                td(data-label="Descripcin y fundamento") El propsito es establecer condiciones sanitarias, fitosanitarias y de bienestar animal, evitando el sufrimiento y el dolor de los animales durante el proceso.
                td(data-label="Soporte Documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 2):] "Con la expedicin de este acto administrativo, se proveen condiciones sanitarias, fitosanitarias y de bienestar animal, evitando as, sufrimiento y dolor a los animales durante, el transporte, manejo y movilizacin de los mismos...".
              tr(data-aos="fade-right")
                td(data-label="Aspecto legal") #[b Tipos de manejo regulados]
                td(data-label="Descripcin y fundamento") La norma se aplica a tres modos principales de movimiento de animales, cada uno con sus reglas especficas.
                td(data-label="Soporte Documental (texto exacto)") #[b Manual (Num. 3.a, b, c):] a) Movilizacin a pie de animales... b) Transporte fluvial de animales... c) Transporte terrestre de animales...

