            tbody
              tr
                td(data-label="Aspecto del manejo)") #[b Manejo humanitario]
                td(data-label="Principio de ejecucin y detalle") Los animales deben ser cargados y descargados con #[b calma], sin generar estrs, ruido ni hostigamiento. Se prohbe el uso de fuerza innecesaria.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915, Manual (Implcito en Numeral 4):] "...los animales debern ser cargados y descargados con #[b calma, sin ruidos ni hostigamiento], ni empleo de #[b fuerza innecesarios] ...".
              tr(data-aos="fade-right")
                td(data-label="Aspecto del manejo)") #[b Uso de equipos seguros]
                td(data-label="Principio de ejecucin y detalle") Se deben utilizar #[b rampas o plataformas] que no tengan puntiagudas salientes, hoyos u orificios, y que tengan pisos de superficie antideslizante.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915, Manual (Implcito en Numeral 4):] "...a travs de #[b rampas o plataformas], sin salientes puntiagudas, hoyos y orificios con #[b pisos de superficies antideslizantes] y/o con puntos de apoyo...".
              tr(data-aos="fade-right")
                td(data-label="Aspecto del manejo)") #[b Inclinacin de rampas]
                td(data-label="Principio de ejecucin y detalle") La inclinacin de la rampa debe ser suave para facilitar el desplazamiento natural de los animales, sin causar esfuerzo excesivo o cadas.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915, Manual (Implcito en Numeral 4):] "...y con mximo #[b 20 grados de inclinacin] que facilite el [cargue/descargue]".
              tr(data-aos="fade-right")
