                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915, Manual (Numeral 4.c):] "Separar por especie, edad, tamao, machos sexualmente maduros de hembras, animales con y sin cuernos, animales que puedan ser hostiles con otros..."
    .titulo.mb-4(data-aos="fade-left")
