        p.mb-0 La movilizacin implica poner en actividad a los animales. El transporte se considera un "conjunto organizado de operaciones" que incluye cargue, traslado, paradas de descanso y descarga.
          br
          br
          | Por lo anterior, la "Teora de la movilizacin" establece el alcance conceptual y los diferentes modos bajo los cuales los animales son trasladados, siendo un trmino ms amplio que el simple "transporte".


