      h5.mb-0 Concepto de movilizacin vs. transporte

    p.mb-4(data-aos="fade-right") El manual distingue entre el concepto amplio de movilizacin y el concepto especfico de transporte de la siguiente manera:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
