                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 2):] "El transporte es el traslado de personas, animales o cosas de un punto a otro a travs de un medio fsico ...".
