                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 2):] "...y para efectos de la resolucin la movilizacin de animales en pie incluye todas aquellas actividades que se desarrollan para mover los animales, ya sea por s mismo o por cualquier otro medio utilizado para trasladar a las especies".
    .titulo.mb-4(data-aos="fade-left")
