    p.mb-4(data-aos="fade-right") La Resolucin 20223040006915 establece que el manual aplica a tres modos de movilizacin, cada uno con requisitos especficos:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 24.
      i Movilizacin regulada
    .row.justify-content-center.align-items-center.mb-4
