                td(data-label="Soporte Documental (texto exacto)") #[b Manual (Numeral 3.a):] "Cuando los propietarios y/o remitentes realicen la #[b movilizacin de animales a pie en vas pblicas o privadas] abiertas al pblico, debern cumplir con las disposiciones contenidas en esta resolucin...".
              tr(data-aos="fade-right")
