                td(data-label="Soporte Documental (texto exacto)") #[b Manual (Numeral 3.b):] "Cuando se realice el #[b transporte de los animales en embarcaciones fluviales], deber cumplir con las disposiciones contenidas en esta resolucin...".
              tr(data-aos="fade-right")
                td(data-label="Tipo de Movilizacin") #[b Transporte terrestre de animales]
                td(data-label="Descripcin y aplicacin") Cuando se realiza el traslado en vehculos automotores de carga (camiones, tractocamiones, etc.). Es el foco principal del manual.
                td(data-label="Soporte Documental (texto exacto)") #[b Manual (Numeral 3.c):] "Cuando se realice el #[b transporte de los animales en vehculo de transporte terrestre automotor de carga] deber cumplir con las disposiciones contenidas en esta resolucin...".
