    p.mb-4(data-aos="fade-right") El manual se fundamenta en un marco legal superior que busca la proteccin animal, incluso al regular el transporte de animales de produccin y reconoce a los animales como seres sintientes (no cosas), obligando a prevenir el dolor y el sufrimiento durante el transporte.

