                  td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Vistos/Considerandos):] menciona las facultades conferidas por los artculos #[b 65 de la Ley 101 de 1993, 4 del Decreto 1500 de 2007], y otros Decretos del Sector Transporte.
