                td(data-label="Trmino") #[b Animal en pie]
                td(data-label="Definicin adoptada por el manual") Designa un individuo vivo de cualquier especie animal.
                td(data-label="Soporte documental (texto exacto)") #[b Manual (Numeral 1):] "Animal en pie: Es un #[b individuo vivo de cualquier especie animal]".
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b Bienestar animal]
                td(data-label="Definicin adoptada por el manual") El estado fsico y mental de un animal en relacin con las condiciones en las que vive y muere.
                td(data-label="Soporte documental (texto exacto)") #[b Manual (Numeral 1):] "Bienestar animal: Designa el #[b estado fsico y mental de un animal] en relacin con las condiciones en las que vive y muere".
