                td(data-label="Soporte documental (texto exacto)") #[b Manual (Numeral 1):] "Capacidad de Carga: Es el #[b mximo tonelaje autorizado] en un vehculo automotor de carga, cuya tara y peso bruto vehicular no exceda los lmites establecidos en las normas vigentes del Ministerio de Transporte".
              tr(data-aos="fade-right")
