                td(data-label="Definicin adoptada por el manual") El nmero o peso corporal de los animales por unidad de rea en el medio de transporte (foco en el espacio por animal).
                td(data-label="Soporte documental (texto exacto)") #[b Manual (Numeral 1 - Definiciones):] "Densidad de carga animal: #[b Nmero o peso corporal de los animales por unidad de rea] en el medio de transporte".
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b Carga de animales]
                td(data-label="Definicin adoptada por el manual") Corresponde al procedimiento por el que se cargan o embarcan los animales en las unidades de transporte.
                td(data-label="Soporte documental (texto exacto)") #[b Manual (Numeral 1):] "Carga de animales: Corresponde al #[b procedimiento por el que se cargan o embarcan los animales] en las unidades de transporte".


