    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.1 Tcnicas de clculo de magnitud de carga

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9.order-lg-1.order-2.mb-lg-0
        p(data-aos="fade-right") La magnitud de carga es el resultado de una doble verificacin que combina la seguridad vial (peso legal) y el bienestar animal (densidad operativa). La normativa colombiana exige que el transportador aplique tcnicas de clculo para asegurar que se cumplan ambos lmites de manera simultnea.
        p(data-aos="fade-right") Por su parte, la Resolucin 20223040006915 establece que la densidad de carga debe determinarse a partir de frmulas de clculo o de las tablas anexas (Apndices).
        p.mb-4(data-aos="fade-right") A continuacin, se detallan las tcnicas de clculo de la magnitud de carga:

      .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema2/img6.svg" alt="Imagen decorativo")

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tcnica N1. Clculo de la densidad de carga operacional (bienestar animal)

