                figure
                  img.img-fluid(src="@/assets/curso/temas/tema2/img8.png" alt="Imagen decorativo")
          p.mb-0 Soporte documental: "La densidad de carga debe determinarse a partir de frmulas de clculo o de las tablas anexas al Manual o de las referencias de la Organizacin Mundial de Sanidad Animal (OIE)."  Manual de Procedimientos, Numeral 4.c.
          

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          .titulo2.mb-4(data-aos="fade-left")
            h5.mb-0 Clculo del nmero mximo de animales (N Mx. de animales) 
          p #[b Proceso:] usar la densidad requerida y el rea disponible para obtener el nmero mximo de cabezas permitidas. Se deben tener en cuenta las siguientes frmulas:

          
          ul.lista-ul
            li
              i.fas.fa-circle
              p.text-bold.mb-0 Bovinos y bufalinos (rea por animal)
          
          .tarjeta.color-secundarios.p-3.mb-4
            .text-center.mb-0
              p.fw-bold
                | N Max = 
                span(style="display:inline-block; vertical-align:middle;")
                  | rea Disponible (m)
                  br
                  span(style="display:inline-block; border-top: 2px solid #000; padding-top: 4px;")
                    | rea Mnima Requerida por Animal (m/animal)
    
        
          ul.lista-ul
            li
              i.fas.fa-circle
              p.text-bold.mb-0 Porcinos y aves (kg/m2). Peso Mximo (kg) = rea Disponible (m2) x Densidad Mxima (kg/m2)

          .tarjeta.color-secundarios.p-3.mb-4
            .text-center.mb-0
              p.fw-bold
                | N Max = 
                span(style="display:inline-block; vertical-align:middle;")
                  | Peso Mximo Total (kg)
                  br
                  span(style="display:inline-block; border-top: 2px solid #000; padding-top: 4px;")
                    | Peso Promedio por Animal (kg)
          p.mb-0 Condicin de Bienestar: el resultado N Max es el lmite superior de carga para cumplir con el bienestar, independientemente de lo que dicte el peso legal.
    
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tcnica N2. Verificacin de la capacidad de carga legal (seguridad vial)

    p.mb-4(data-aos="fade-right") Esta tcnica se enfoca en asegurar que el peso total de la carga viva no exceda el lmite legal autorizado para el vehculo, protegiendo la seguridad vial y la estructura del automotor.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          .titulo2.mb-4(data-aos="fade-left")
            h5.mb-0 Determinacin del lmite de peso legal (P. Legal)
          .row.justify-content-center.align-items-center.mb-3
              .col-lg-9.order-lg-1.order-2
                p #[b Proceso:] consultar la licencia de trnsito del vehculo (registro RUNT) para verificar el mximo tonelaje autorizado (Capacidad de Carga).
                p #[b Soporte documental (definicin):] "Capacidad de carga: es el mximo tonelaje autorizado en un vehculo automotor de carga, cuya tara y peso bruto vehicular, no exceda los lmites establecidos en las normas vigentes del Ministerio de Transporte. No podr alterarse la capacidad de carga consignada en la licencia de trnsito".  Manual de Procedimientos, Numeral 1. Definiciones.
                h5 Frmula conceptual:
                .tarjeta.color-secundarios.p-3
                  p.text-bold.text-center.mb-0 Peso Vivo\ (kg) = N unidades x Peso Promedio por Animal

              .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
                figure
                  img.img-fluid(src="@/assets/curso/temas/tema2/img9.png" alt="Imagen decorativo")
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          .titulo2.mb-4(data-aos="fade-left")
            h5.mb-0 Verificacin del Lmite Legal
          .row.justify-content-center.align-items-center.mb-2
              .col-lg-12
                p #[b Proceso:] el peso total de la carga (Peso vivo animales) + (Peso de la carrocera de transporte) debe ser menor o igual a la capacidad de carga autorizada en el vehculo.
          figure.text-center
            img.img-fluid(src="@/assets/curso/temas/tema2/img10.png" alt="Imagen decorativa")
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          .titulo2.mb-4(data-aos="fade-left")
            h5.mb-0 Principio del lmite restrictivo
          .row.justify-content-center.align-items-center.mb-3
              .col-lg-10.order-lg-1.order-2
                p La magnitud de carga final y segura que debe aplicar el transportador es el valor ms bajo resultante entre la densidad operacional (Tcnica N1) y la capacidad de carga legal (Tcnica N2).
                p Si:
                .tarjeta.color-primarios.p-3
                  ul.lista-ul.mb-0
                    li
                      i.fas.fa-horse-head
                      p.mb-0 El clculo de densidad (Tcnica N1) permite cargar 20 bovinos.
                    li
                      i.fas.fa-horse-head
                      p.mb-0 El clculo de peso legal (Tcnica N2) solo permite cargar 18 bovinos (por restriccin de peso).

              .col-lg-2.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
                figure
                  img.img-fluid(src="@/assets/curso/temas/tema2/img11.png" alt="Imagen decorativo")
          p.mb-0 #[b Entonces:] la magnitud de carga correcta y obligatoria es de 18 bovinos, ya que la ley exige cumplir con el lmite ms estricto entre el bienestar animal y la seguridad vial.

    separador
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.2 Bienestar y proteccin animal en transporte terrestre
    .bloque-texto-gg.color-primario.p-3.mb-4
      .bloque-texto-gg__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema2/img12.svg')})`}"
      )
      .bloque-texto-gg__texto.mt-5.mt-lg-0.p-2
        p.mb-0 Constituyen el eje central de toda la normativa regulatoria en Colombia. El enfoque no solo busca la legalidad de la operacin, sino tambin garantizar que los animales, reconocidos como seres sintientes, no padezcan sufrimiento innecesario durante la movilizacin
          br
          br
          | El bienestar animal, segn la normativa colombiana, se refiere al conjunto de condiciones y prcticas que garantizan que los animales de produccin estn libres de hambre, sed, dolor, miedo y puedan expresar su comportamiento natural, promoviendo su salud fsica y emocional durante todas las etapas de su vida productiva.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Normativa (regulatorio)
    p(data-aos="fade-right") La normativa que rige el bienestar animal en el transporte terrestre se soporta en disposiciones de alto nivel y se materializa en el manual de procedimientos adoptado de manera conjunta por las autoridades competentes.
    p.mb-4(data-aos="fade-right") A continuacin, se detallan algunas regulaciones:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 27.
      i Regulaciones bienestar y proteccin animal
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Norma regulatoria
                th Propsito y alcance
                th Soportes documentales clave
            tbody
              tr(data-aos="fade-right")
                td(data-label="Norma regulatoria") #[b Ley 1774 de 2016]
                td(data-label="Propsito y alcance") #[b Estatuto Nacional de Proteccin Animal]. Modifica el Cdigo Civil para reconocer a los animales como #[b seres sintientes] y no cosas, sentando la base legal para que cualquier actividad (incluido el transporte) deba garantizar su bienestar y evitar el maltrato.
                td(data-label="Soportes documentales clave") Referente legal externo al manual, pero fundamental.
              tr(data-aos="fade-right")
                td(data-label="Norma regulatoria") #[b Resolucin 20223040006915 de 2022]
                td(data-label="Propsito y alcance") #[b Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie.] Es el documento tcnico operativo que establece las reglas detalladas que deben seguir transportadores, conductores y propietarios para cumplir con el bienestar animal en todas las etapas del transporte terrestre y fluvial.
                td(data-label="Soportes documentales clave") #[b Resolucin 20223040006915 (Art. 1, 2):] adopta el manual para el transporte, manejo y movilizacin de especies de produccin.
              tr(data-aos="fade-right")
                td(data-label="Norma regulatoria") #[b Resoluciones del MADR]
                td(data-label="Propsito y alcance") Normas especficas de bienestar animal por especie (bovina, porcina, aviar, etc.), las cuales el personal de transporte debe conocer.
                td(data-label="Soportes documentales clave") #[b Plan-capacitacin (Numeral 1):] menciona el cumplimiento de la normatividad expedida por el #[b Ministerio de Agricultura y Desarrollo Rural (MADR)] para cada especie.

    p.mb-4 #[b Normativo:] el marco legal se estructura jerrquicamente as:
    .row.mb-4
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center 1.	Ley 84 de 1989 (Estatuto de Proteccin)
              p Establece el deber de proteccin y prohbe la crueldad
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img14.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center 2.	Ley 1774 de 2016
              p Es la ley clave. Reconoce a los animales como seres sintientes (no cosas) y tipifica el maltrato animal (incluyendo el causado por negligencia en el transporte) como un delito penal y civil.
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img15.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center 3.	Resolucin 20223040006915
              p Es el manual tcnico que traduce las leyes anteriores en procedimientos obligatorios (el "cmo hacer") para el transporte.


    p.mb-4(data-aos="fade-right") #[b Bienestar:] se pasa de un enfoque de "evitar la crueldad" a un enfoque proactivo de "garantizar el bienestar".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Principios del bienestar y proteccin animal

    p.mb-4(data-aos="fade-right") Los principios rectores de la normativa de transporte se centran en la prevencin de riesgos y la garanta de condiciones mnimas de vida para los animales; al respecto, es importante tener en cuenta:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 28.
      i Principios rectores
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Principio
                th Aplicacin en el transporte
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Principio") #[b Prevencin del sufrimiento]
                td(data-label="Aplicacin en el transporte") Es la razn de ser de la norma. Se busca evitar el #[b sufrimiento y dolor] causado por hacinamiento, golpes, ayuno prolongado, estrs trmico o falta de ventilacin.
                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 2):] "...se proveen condiciones sanitarias, fitosanitarias y de #[b bienestar animal, evitando as, sufrimiento y dolor] a los animales durante, el transporte...".
              tr(data-aos="fade-right")
                td(data-label="Principio") #[b Inocuidad y calidad]
                td(data-label="Aplicacin en el transporte") El bienestar del animal est ligado directamente a la calidad del producto final (carne). Un animal estresado o maltratado genera prdidas y afecta la inocuidad.
                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 2):] "...mejorando en este sentido la #[b calidad e inocuidad de la carne], en beneficio de los consumidores".
              tr(data-aos="fade-right")
                td(data-label="Principio") #[b Seleccin de Animales Aptos]
                td(data-label="Aplicacin en el transporte") Principio de proteccin que exige #[b seleccionar y no cargar] animales que, por su condicin, no puedan soportar el viaje sin sufrir.
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Numeral 4.a):] "exige observar y seleccionar para detectar animales #[b no aptos para el transporte], tales como: i) Enfermos, lesionados o con prolapsos. ii) Aquellos que no puedan permanecer de pie sin ayuda".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Las Cinco Libertades / Los Cinco Dominios

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9.order-lg-1.order-2.mb-lg-0
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          p El concepto de bienestar animal adoptado internacionalmente y referenciado indirectamente en la normativa colombiana se basa en las Cinco Libertades, ya su vez, puede ser abordado desde el marco ms integral de los Cinco Dominios.
          p.mb-0 Entrando en detalle, las Cinco Libertades son el estndar de oro de la Organizacin Mundial de Sanidad Animal (OIE), adoptado por el ICA, que define las condiciones ideales para el bienestar animal y se representa de la siguiente manera:
      .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema2/img16.svg" alt="Imagen decorativo")

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.movil
        figure
          img.img-fluid(src="@/assets/curso/temas/tema2/img17_1.svg" alt="Imagen decorativo")
      .col-lg-10.desktop
        ImagenInfografica.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema2/img17.svg', alt='Texto que describa la imagen')
          .tarjeta.color-secundario.p-3(x="42.8%" y="80%" numero="+")
            p Suministro de agua y alimento o periodos de descanso programados para proveerlos, especialmente en viajes largos (cada 8 horas).
            p.mb-0 Se garantiza con un ayuno correcto (no excesivo) y con los periodos obligatorios de descanso, hidratacin y alimentacin en jornadas largas (Res. 20223040006915).
          .tarjeta.color-secundario.p-3(x="30.5%" y="43.7%" numero="+")
            p Uso de pisos antideslizantes, separacin de animales hostiles y uso de cama si es necesario.
            p.mb-0 Se garantiza con vehculos adecuados (pisos antideslizantes, ventilacin, techos).
          .tarjeta.color-secundario.p-3(x="71.8%" y="19%" numero="+")
            p Garanta de densidad de carga suficiente (espacio para animales) que les permita levantarse, echarse y cambiar de postura.
            p.mb-0 Se garantiza con una densidad de carga correcta (que permita al menos echarse y pararse).
          .tarjeta.color-secundario.p-3(x="25.5%" y="23%" numero="+")
            p.mb-0 Seleccin previa para excluir animales no aptos, manejo cuidadoso, diseo del vehculo sin salientes y aviones de contingencia para emergencias. 
          .tarjeta.color-secundario.p-3(x="62%" y="75.8%" numero="+")
            p Manejo silencioso y sin hostigamiento, uso de tcnicas de manejo adecuadas y conocimiento del comportamiento animal por parte del personal.
            p.mb-0 Se garantiza con un manejo etolgico (sin gritos, sin picaduras excesivas) y una conduccin suave.
           
    p.mb-4(data-aos="fade-right") Por su parte, aunque el manual se basa principalmente en la prevencin del sufrimiento (Ley 1774), el Plan de Capacitacin en Bienestar Animal integra un enfoque ms amplio, como el de los Cinco Dominios, que evala las condiciones del animal, as:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.tarjeta.color-secundario.p-4(data-aos="fade-right")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            ul.lista-ul
              li
                i.fas.fa-horse-head
                p.mb-0 #[b Nutricin]
                  br
                  | Hambre y sed.
              li
                i.fas.fa-horse-head
                p.mb-0 #[b Ambiente]
                  br
                  | Incomodidad por entorno.
              li
                i.fas.fa-horse-head
                p.mb-0 #[b Salud]
                  br
                  | Dolor, lesiones, enfermedades.
              li
                i.fas.fa-horse-head
                p.mb-0 #[b Comportamiento]
                  br
                  | Expresin de comportamientos naturales.
              li
                i.fas.fa-horse-head
                p.mb-0 #[b Estado mental]
                  br
                  | Miedo, angustia y bienestar general.
          .col-lg-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
            figure
              img.img-fluid(src="@/assets/curso/temas/tema2/img18_1.png" alt="Imagen decorativo")
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Glosario de normativa especfica del bienestar
      
    p.mb-4(data-aos="fade-right") El manual de procedimientos (Numeral 1. Definiciones) fija los trminos esenciales para asegurar que la normativa sea aplicada correctamente en pro del bienestar animal, destacando:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 29.
      i Glosario de normativa
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Trmino
                th Definicin adoptada por el manual
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b Bienestar animal]
                td(data-label="Definicin adoptada por el manual") El estado fsico y mental de un animal en relacin con las #[b condiciones en las que vive y muere].
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Nm. 1):] "Designa el estado fsico y mental de un animal en relacin con las condiciones en las que vive y muere".
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b Densidad de carga animal]
                td(data-label="Definicin adoptada por el manual") El #[b nmero o peso corporal de los animales por unidad de rea] en el medio de transporte. (Es la clave para prevenir el hacinamiento y el estrs por calor).
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 1):] "Nmero o peso corporal de los animales por unidad de rea en el medio de transporte".
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b No apto para el transporte]
                td(data-label="Definicin adoptada por el manual") Animales con condiciones fsicas o de salud que les causaran un #[b sufrimiento injustificado] al ser movilizados (ej. enfermos, lesionados o que no pueden estar de pie).
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 4.a):] "Menciona las condiciones de animales que deben ser detectados como no aptos".
              tr(data-aos="fade-right")
                td(data-label="Trmino") #[b Carga de animales]
                td(data-label="Definicin adoptada por el manual") El procedimiento por el que se cargan o embarcan los animales en las unidades de transporte. (Debe hacerse con calma y sin hostigamiento).
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 1):] "Corresponde al procedimiento por el que se cargan o embarcan los animales en las unidades de transporte".
    separador
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.3 Tcnicas de conduccin

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-3.col-sm-6.col-8.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema2/img18.png" alt="Imagen decorativo")
      .col-lg-9(data-aos="fade-right")
        .tarjeta.color-primarios3.p-4
          p La conduccin es un factor determinante en el bienestar animal durante el transporte terrestre. Una conduccin brusca o inadecuada genera estrs, cadas, contusiones y lesiones graves, afectando la calidad e inocuidad de la carga.
          p.mb-0 Es as como el manual de procedimientos (Resolucin 20223040006915 de 2022) exige tcnicas especficas de conduccin que minimicen el impacto del movimiento en los animales.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Principios de conduccin preventiva y suave
      
    p.mb-4(data-aos="fade-right") El conductor o tripulante debe aplicar principios de manejo vehicular que eviten el movimiento excesivo de los animales y el estrs, en los cuales aplique lo siguiente:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 30.
      i Principios de conduccin
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Principio de conduccin
                th Detalle de la tcnica y objetivo
                th Soporte documental (regla general)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Principio de conduccin") #[b Aceleracin y frenado suaves]
                td(data-label="Detalle de la tcnica y objetivo") La conduccin debe ser #[b suave y estable]. Se deben evitar los #[b frenazos bruscos] y las aceleraciones repentinas que desequilibren a los animales y provoquen cadas o choques contra las paredes del vehculo.
                td(data-label="Soporte documental (regla general)") #[b Manual de procedimiento (Implcito en Numeral 4):] exige una conduccin que #[b evite el estrs y las lesiones], lo cual requiere maniobras controladas.
              tr(data-aos="fade-right")
                td(data-label="Principio de conduccin") #[b Velocidad moderada]
                td(data-label="Detalle de la tcnica y objetivo") La velocidad debe ser apropiada para las condiciones de la va, el trfico y el clima, pero siempre #[b moderada] para reducir la inercia del movimiento de la carga viva.
                td(data-label="Soporte documental (regla general)") #[b Manual de procedimiento (Implcito en Numeral 4):] se enfoca en garantizar la #[b estabilidad del vehculo y la carga], lo cual se logra con velocidad controlada.
              tr(data-aos="fade-right")
                td(data-label="Principio de conduccin") #[b Trazado de curvas]
                td(data-label="Detalle de la tcnica y objetivo") Las curvas y los giros deben tomarse a #[b baja velocidad y de manera gradual] para evitar que la fuerza centrfuga empuje a los animales contra las divisiones o los lados del vehculo.
                td(data-label="Soporte documental (regla general)") #[b Manual de procedimiento (Implcito en Numeral 4):] las divisiones deben ser fuertes para resistir la presin, pero la conduccin debe mitigar esta presin.
              tr(data-aos="fade-right")
                td(data-label="Principio de conduccin") #[b Conduccin anticipada]
                td(data-label="Detalle de la tcnica y objetivo") Mantener una distancia de seguridad suficiente que permita al conductor #[b anticipar el frenado] y realizarlo de forma progresiva, minimizando la necesidad de frenadas de emergencia.
                td(data-label="Soporte documental (regla general)") #[b Manual de procedimiento (Implcito en Numeral 4):] busca #[b minimizar el riesgo de traumatismos] o fracturas en los animales.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Protocolos de inspeccin y descanso
      
    p.mb-4(data-aos="fade-right") Las tcnicas de conduccin tambin incluyen protocolos que regulan los tiempos de viaje y la necesidad de supervisin constante de la carga, destacndose de ello lo siguiente:

    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
        h5 Tabla 31.
        i Protocolos en tcnicas de conduccin
      .row.justify-content-center.align-items-center.mb-4 
        .col-lg-10
          .tabla-a.color-acento-contenido.table-wrapper
            table.tabla-responsive
              thead
                tr(data-aos="fade-right")
                  th Protocolo de viaje
                  th Exigencia y periodicidad
                  th Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td(data-label="Protocolo de viaje") #[b Descanso obligatorio]
                  td(data-label="Exigencia y periodicidad") El conductor debe #[b descansar al menos ocho (8) horas] despus de un tiempo mximo de ocho #[b (8) horas de conduccin], para mantener el estado de alerta y evitar la fatiga que compromete la seguridad y el manejo suave.
                  td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Numeral 4.i):] "El conductor o tripulante deber #[b descansar al menos ocho (8) horas], una vez haya conducido #[b ocho (8) horas continuas]".
                tr(data-aos="fade-right")
                  td(data-label="Protocolo de viaje") #[b Inspeccin en ruta]
                  td(data-label="Exigencia y periodicidad") El conductor o tripulante debe realizar una #[b inspeccin visual] del vehculo y de los animales en movimiento (en paradas seguras) para detectar signos de estrs, cadas, lesiones o problemas de ventilacin.
                  td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Numeral 4.g):] "El conductor o tripulante deber realizar la #[b inspeccin de la unidad de transporte] de animales y de los animales en movimiento, por lo menos cada #[b cuatro (4) horas] durante el viaje".
                tr(data-aos="fade-right")
                  td(data-label="Protocolo de viaje") #[b Atencin a la ventilacin]
                  td(data-label="Exigencia y periodicidad") Si no se cuenta con ventilacin mecnica, el conductor debe estar atento a las condiciones de microclima (temperatura y ventilacin) para evitar el estrs trmico, ajustando la velocidad o buscando zonas de sombra si es necesario.
                  td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Numeral 4.f):] exige contar con dispositivos para mantener la #[b ventilacin constante], asegurando una temperatura entre 5 C y 30 C.
      
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tcnicas de conduccin en condiciones adversas
    p.mb-4 El manual exige al conductor aplicar tcnicas modificadas de manejo al encontrarse con situaciones que aumentan el riesgo de dao a los animales. Entre ellas se encuentran: 
    .row.justify-content-center.mb-4
        .col-lg-5.mb-4.mb-lg-0(data-aos="fade-right")
          .tarjeta-avatar(style="padding-top: 0px !important;").h-100          
              .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
                img(src='@/assets/curso/temas/tema2/img19.png', alt='Imagen decorativa')
                .p-4
                  h5 Vas irregulares (destapadas)
                  p La velocidad debe reducirse excesivamente para mitigar las vibraciones y los golpes transmitidos a la carrocera, que pueden causar hematomas y fatiga en los animales.
        .col-lg-5(data-aos="fade-right")
          .tarjeta-avatar(style="padding-top: 0px !important;").h-100            
              .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
                img(src='@/assets/curso/temas/tema2/img20.png', alt='Imagen decorativa')
                .p-4
                  h5 Clima extremo (lluvia/calor)
                  p Vehculos no motorizados halados por una unidad tractora. Existen de tipo:
                  ul.lista-ul
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Lluvia:] aumente la distancia de frenado y reduzca la velocidad para evitar derrapes y movimientos bruscos.
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Calor (estrs trmico):] la conduccin en horas de la noche o madrugada es preferible. Si el viaje es de da, se debe priorizar la ventilacin constante y minimizar las paradas en zonas expuestas al sol.
    .row.justify-content-center.align-items-center
      .col-lg-10(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img21.svg' alt='AvatarTop')
          .tarjeta.color-primario.w-100(style="padding-top: 5px !important")
            .text-white.p-4
              p.mb-0(style="margin-top: 42px;") Estas tcnicas de conduccin garantizan que el bienestar animal se mantenga durante el traslado, convirtiendo al conductor en un factor activo en la proteccin de la carga viva.
    separador
    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.4 Tipos y tcnicas de inspeccin de vehculos destinados al transporte de animales en pie
    
    .bloque-texto-g.bloque-texto-g--inverso.color-primario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema2/img22.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 La #[b inspeccin de vehculos para el transporte de animales en pie] es un procedimiento esencial para el cumplimiento del bienestar animal y las normas sanitarias. 
          br
          br 
          | El proceso implica diferentes tipos de inspeccin, tanto operacionales (a cargo del transportador) como de control (a cargo de la autoridad), regidos por el Manual de Procedimientos (Resolucin 20223040006915 de 2022).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de inspeccin
      
    p.mb-4(data-aos="fade-right") Los tipos de inspeccin se definen por el momento de ejecucin y la entidad responsable, de la siguiente manera:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 32.
      i Tipos de inspeccin
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Tipo de inspeccin
                th Responsable
                th Momento de aplicacin
                th Soportes documentales (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Tipo de inspeccin") #[b Inspeccin previa al cargue]
                td(data-label="Responsable") Propietario, tenedor, poseedor, o quien est delegado/autorice.
                td(data-label="Momento de aplicacin") #[b Antes] de cargar los animales, en la finca o punto de partida.
                td(data-label="Soportes documentales (texto exacto)") #[b Manual (Num. 4.a):] exige "Actividades previas al cargue" donde el propietario debe #[b observar y seleccionar los animales, para detectar animales no aptos para el transporte] y realizar la limpieza del vehculo.
              tr(data-aos="fade-right")
                td(data-label="Tipo de inspeccin") #[b Inspeccin en ruta]
                td(data-label="Responsable") Conductor o tripulante.
                td(data-label="Momento de aplicacin") #[b Durante] el traslado, en paradas seguras y programadas.
                td(data-label="Soportes documentales (texto exacto)") #[b Manual (Nm. 4.g):] "El conductor o tripulante deber realizar la #[b inspeccin de la unidad de transporte de animales] y de los animales en movimiento, por lo menos #[b cada cuatro (4) horas] durante el viaje".
              tr(data-aos="fade-right")
                td(data-label="Tipo de inspeccin") #[b Inspeccin y control oficial]
                td(data-label="Responsable") Funcionarios del Instituto Colombiano Agropecuario (ICA) o autoridades de trnsito.
                td(data-label="Momento de aplicacin") En cualquier momento o punto de control a lo largo de la ruta de movilizacin.
                td(data-label="Soportes documentales (texto exacto)") #[b Resolucin 20223040006915 (Art. 4, Pargrafo):] "Las personas naturales y/o jurdicas... estn en la obligacin de permitir la #[b inspeccin, vigilancia y control], por parte de los funcionarios del Instituto Colombiano Agropecuario - ICA, o quien este delega o autorice".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tcnicas y puntos de inspeccin (#[i Checklist] del vehculo)
      
    p.mb-4(data-aos="fade-right") El conductor y la autoridad deben verificar que la unidad de transporte cumpla con los requisitos estructurales, de seguridad y de higiene, segn los siguientes puntos:

    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Inspeccin de estructura y materiales")
        .row.justify-content-center.align-items-center
          .col-lg-12
            p.mb-4 La carrocera o unidad de carga debe ser segura y minimizar el riesgo de lesiones, as:
            .titulo-sexto.color-acento-contenido.offset-0(data-aos="fade-left")
              h5 Tabla 33.
              i Estructura y materiales
            .row.justify-content-center.align-items-center.mb-4 
              .col-lg-10
                .tabla-a.color-acento-contenido.table-wrapper
                  table.tabla-responsive(style="background-color: white;")
                    thead
                      tr
                        th Punto por inspeccionar
                        th Tcnica de verificacin
                        th Soporte documental (texto exacto)
                    tbody
                      tr
                        td(data-label="Punto por inspeccionar") #[b Estado general]
                        td(data-label="Tcnica de verificacin") Inspeccin visual para asegurar que el vehculo y los contenedores estn en #[b estado adecuado].
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 2.2.ci):] "Ser #[b seguros] y tener un #[b adecuado estado] ...".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Materiales y bordes]
                        td(data-label="Tcnica de verificacin") Verificacin de que el material sea #[b resistente al impacto], con #[b superficies lisas y bordes redondeados].
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Nm. 2.2.ci):] "...elaborados de materiales #[b resistentes al impacto], de #[b superficies lisas, con bordes redondeados ...]".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Techo y cubierta]
                        td(data-label="Tcnica de verificacin") Confirmar la existencia de una cubierta o techo para proteger a los animales de las condiciones climticas extremas (sol directo, lluvia).
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Nm. 2.2.ci):] "...debern contar con #[b techo o cubierta] que proteja a los animales de las condiciones climticas adversas".
                        
      .row(titulo="Inspeccin del piso y contencin")
        .row.justify-content-center.align-items-center
          .col-lg-12
            p.mb-4 El piso es crtico para prevenir cadas, lesiones y la contaminacin ambiental. Por ello, es importante realizar lo siguiente:
            .titulo-sexto.color-acento-contenido.offset-0(data-aos="fade-left")
              h5 Tabla 34.
              i Piso y contencin
            .row.justify-content-center.align-items-center.mb-4 
              .col-lg-10
                .tabla-a.color-acento-contenido.table-wrapper
                  table.tabla-responsive(style="background-color: white;")
                    thead
                      tr
                        th Punto por inspeccionar
                        th Tcnica de verificacin
                        th Soporte documental (texto exacto)
                    tbody
                      tr
                        td(data-label="Punto por inspeccionar") #[b Antideslizante]
                        td(data-label="Tcnica de verificacin") Revise que la superficie del piso ofrezca traccin para evitar que los animales resbalen y caigan.
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Nm. 2.2.c.ii):] "El piso deber ser #[b antideslizante] ...".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Estanqueidad (filtracin)]
                        td(data-label="Tcnica de verificacin") Verifique que el piso cuente con un sistema que #[b evite la filtracin] de heces y orina hacia la va pblica, conteniendo los desechos.
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Nm. 2.2.c.ii):] "...contar con un sistema que #[b evite la filtracin] de orina y heces hacia la va pblica".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Limpieza y desinfeccin]
                        td(data-label="Tcnica de verificacin") Previo al cargue, inspeccionar la #[b limpieza] del vehculo. Los materiales deben permitir el #[b lavado y la desinfeccin].
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 2.2.ci):] "...que permita su lavado y desinfeccin".
      .row(titulo="Inspeccin de condiciones de manejo (cargue y traslado)")
        .row.justify-content-center.align-items-center
          .col-lg-12
            p.mb-4 Se revisan los elementos que afectan la densidad de carga y el #[i confort] durante el viaje. Se verifican los siguientes elementos:
            .titulo-sexto.color-acento-contenido.offset-0(data-aos="fade-left")
              h5 Tabla 35.
              i Condiciones de manejo
            .row.justify-content-center.align-items-center.mb-4 
              .col-lg-10
                .tabla-a.color-acento-contenido.table-wrapper
                  table.tabla-responsive(style="background-color: white;")
                    thead
                      tr
                        th Punto por inspeccionar
                        th Tcnica de verificacin
                        th Soporte documental (texto exacto)
                    tbody
                      tr
                        td(data-label="Punto por inspeccionar") #[b Rampas de carga]
                        td(data-label="Tcnica de verificacin") Hay que confirmar que la rampa est libre de salientes, sea antideslizante y la inclinacin no sea superior al mximo permitido.
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 4.a - Prrafo):] deben ser "seguras... con #[b pisos de superficies antideslizantes] ... y con #[b mximo 20 grados de inclinacin] que facilite el [cargue]."
                      tr
                        td(data-label="Punto por inspeccionar") #[b Sistemas de ventilacin]
                        td(data-label="Tcnica de verificacin") Verifique la existencia de dispositivos (aberturas laterales, ventilacin mecnica) para mantener el flujo de aire y la temperatura adecuada.
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 4.f):] "Los vehculos debern contar con un sistema para mantener la #[b ventilacin constante] y asegurar la temperatura...".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Divisiones y separacin]
                        td(data-label="Tcnica de verificacin") Inspeccionar la existencia y #[b fortaleza] de las divisiones para separar grupos (toros de vacas, animales con cuernos de sin cuernos, diferentes especies).
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 2.2.c.iii):] "Contar con #[b divisiones o barreras fuertes] que permitan la #[b separacin de los animales] ...".
                      tr
                        td(data-label="Punto por inspeccionar") #[b Dispositivos de amarre]
                        td(data-label="Tcnica de verificacin") Si aplica a la especie (ej. equinos o bovinos individuales), revise el estado de los dispositivos de amarre y que sean de fcil liberacin en caso de emergencia.
                        td(data-label="Soporte documental (texto exacto)") #[b Manual (Num. 2.2.c.iii - Implcito):] asegurar que las sujeciones utilizadas no causen sufrimiento.

    separador
    #t_2_5.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.5 Protocolos de bioseguridad
    p(data-aos="fade-right") Los protocolos de bioseguridad en el transporte de animales en pie son un conjunto de medidas sanitarias y de higiene obligatorias, destinadas a prevenir la transmisin de enfermedades entre los animales y la contaminacin del medio ambiente y de la va pblica. Estos protocolos se fundamentan en la necesidad de proveer condiciones sanitarias, fitosanitarias y de bienestar animal.
    p.mb-4(data-aos="fade-right") Partiendo de lo anterior, se invita a analizar la siguiente imagen:

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-left")
          h5 Figura 3.
          i  Protocolos de bioseguridad
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.movil(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema2/img23_1.png', alt='Figura 3 que contiene diferentes tipos de acciones de bioseguridad que se realizan en los vehculos de transporte de animales en pie; los cuales van desde el lavado e higiene, tanto de los operarios como de los mismos vehculos.')
      .col-lg-10.desktop(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema2/img23.png', alt='Figura 3 que contiene diferentes tipos de acciones de bioseguridad que se realizan en los vehculos de transporte de animales en pie; los cuales van desde el lavado e higiene, tanto de los operarios como de los mismos vehculos.')

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Bioseguridad estructural y de diseo
      
    p.mb-4(data-aos="fade-right") La unidad de transporte debe estar diseada con materiales y caractersticas que faciliten la higiene y contengan los contaminantes biolgicos. Adems, tener en cuenta lo siguiente:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 36.
      i Requisitos de bioseguridad
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Requisito de bioseguridad
                th Detalle de la tcnica y objetivo
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Requisito de bioseguridad") #[b Materiales y desinfeccin]
                td(data-label="Detalle de la tcnica y objetivo") Los materiales de la carrocera, incluidos los guacales o cajas, deben ser de #[b superficies lisas] y resistentes para permitir su #[b lavado y desinfeccin] de manera efectiva despus de cada uso, evitando la acumulacin de materia orgnica.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Numeral 2.2.ci):] "Ser seguros, tener un adecuado estado y elaborados de materiales resistentes al impacto, de #[b superficies lisas], con bordes redondeados, que permitan su #[b lavado y desinfeccin]".
              tr(data-aos="fade-right")
                td(data-label="Requisito de bioseguridad") #[b Piso estanco]
                td(data-label="Detalle de la tcnica y objetivo") El piso del vehculo debe ser #[b antideslizante] y contar con un sistema que garantice la #[b estanqueidad]; es decir, que #[b evite la filtracin] de heces, orina y otros fluidos biolgicos hacia la va pblica, conteniendo estos desechos dentro del vehculo.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Numeral 2.2.c.ii):] "El piso deber ser antideslizante y contar con un sistema que #[b evite la filtracin de orina y heces] hacia la va pblica".
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Protocolos de bioseguridad operacional (limpieza y manejo)
      
    p.mb-4(data-aos="fade-right") Estos protocolos son las acciones que deben realizar el transportador y el conductor en cada etapa del proceso de movilizacin. Para su correcto accionar deben aplicar lo siguiente:

    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
        h5 Tabla 37.
        i Protocolo operacional
      .row.justify-content-center.align-items-center.mb-4 
        .col-lg-10
          .tabla-a.color-acento-contenido.table-wrapper
            table.tabla-responsive
              thead
                tr(data-aos="fade-right")
                  th Protocolo operacional
                  th Responsable y detalle de la accin
                  th Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td(data-label="Protocolo operacional") #[b Limpieza previa al cargue]
                  td(data-label="Responsable y detalle de la accin") La limpieza y desinfeccin del vehculo debe ser una #[b actividad previa a la carga] de los animales, para asegurar que no haya residuos biolgicos de cargas anteriores que puedan transmitir enfermedades.
                  td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Implcito en Numeral 4.a):] la limpieza del vehculo es parte de las #[b actividades previas al cargue] (donde se selecciona el animal).
                tr(data-aos="fade-right")
                  td(data-label="Protocolo operacional") #[b Contencin de cajas (aves)]
                  td(data-label="Responsable y detalle de la accin") En el caso de especies avcolas transportadas en cajas (ej. pollitos de un da), estas deben ser seguras y estar dispuestas de manera que #[b eviten su desplazamiento o volcamiento], lo cual evita la dispersin de desechos y fluidos en caso de accidente.
                  td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Numeral 2.2.cv):] "Estar #[b fijados o dispuestos de manera tal, que se evite su desplazamiento y/o volcamiento]".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Manejo de contingencias bioseguras
      
    p.mb-4(data-aos="fade-right") La bioseguridad tambin se extiende a la forma en que se manejan las emergencias sanitarias que puedan surgir durante el viaje y las cuales deben tener en cuenta lo siguiente:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 38.
      i Contingencias bioseguras
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Contingencias bioseguras
                th Acciones requeridas
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Contingencias bioseguras") #[b Disposicin de muertos o enfermos]
                td(data-label="Acciones requeridas") En caso de #[b animales muertos] o enfermos que deban ser sacrificados en ruta, el manual exige un protocolo que incluye la #[b disposicin de los animales muertos] de formas bioseguras.
                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 6):] "Las van desde la ayuda o atencin inmediata al animal, informar a la oficina del Instituto Colombiano Agropecuario ICA ms cercana, #[b sacrificio y disposiciones de los animales muertos]".
              tr(data-aos="fade-right")
                td(data-label="Contingencias bioseguras") #[b Notificacin a la autoridad]
                td(data-label="Acciones requeridas") La notificacin inmediata al #[b ICA] es un protocolo de bioseguridad clave para el control oficial, permitiendo a la autoridad activar medidas sanitarias y de rastreo si es necesario.
                td(data-label="Soporte documental (texto exacto)") #[b ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (Pg. 6):] menciona la necesidad de #[b informar a la oficina del Instituto Colombiano Agropecuario ICA ms cercana] en caso de animales enfermos o muertos.

    separador
    #t_2_6.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.6 Tcnicas de limpieza y aseo, tipos de producto
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9
        p(data-aos="fade-right") La limpieza y el aseo de las unidades de transporte son un pilar fundamental de los protocolos de bioseguridad y las condiciones sanitarias exigidas por la normativa. Las tcnicas se centran en la eliminacin de materia orgnica y la desinfeccin para prevenir la propagacin de enfermedades.
        p.mb-4(data-aos="fade-right") La documentacin (Resolucin 20223040006915) se enfoca en los requisitos estructurales del vehculo que facilitan esta tarea y la obligatoriedad de realizarla.
      .col-lg-3.d-none.d-lg-block(data-aos="fade-right")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema2/img18_1.svg" alt="Imagen decorativo")

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tcnicas y requisitos de limpieza y aseo
      
    p.mb-4(data-aos="fade-right") El proceso de limpieza y aseo se rige por la necesidad de ser efectivo, constante y bioseguro. Adems, tiene en cuenta:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 39.
      i Estndares de limpieza y aseo
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Tcnica/requisito
                th Detalle y objetivo del procedimiento
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Tcnica/requisito") #[b Diseo para la limpieza]
                td(data-label="Detalle y objetivo del procedimiento") La tcnica principal de diseo es la de utilizar materiales de #[b superficies lisas y bordes redondeados]. Esto facilita el raspado y el lavado, ya que evita que la materia orgnica (heces, orina, sangre) se adhiera o se acumule en grietas y esquinas, que son focos de contaminacin.
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 2.2.ci):] "...elaborados de materiales resistentes al impacto, de #[b superficies lisas, con bordes redondeados], que permitan su #[b lavado y desinfeccin]".
              tr(data-aos="fade-right")
                td(data-label="Tcnica/requisito") #[b Tcnica de estanqueidad]
                td(data-label="Detalle y objetivo del procedimiento") El vehculo debe contar con un sistema para #[b evitar la filtracin] de fluidos biolgicos hacia la va pblica, lo que implica una tcnica de #[b contencin total] de los desechos durante el viaje. La limpieza posterior debe centrarse en el retiro seguro de estos desechos estancados.
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 2.2.c.ii):] "El piso deber ser antideslizante y contar con un sistema que #[b evite la filtracin de orina y heces] hacia la va pblica".
              tr(data-aos="fade-right")
                td(data-label="Tcnica/requisito") #[b Periodicidad de la bioseguridad]
                td(data-label="Detalle y objetivo del procedimiento") La tcnica de bioseguridad exige que la limpieza y desinfeccin del vehculo se realice #[b antes del transporte] de los animales, para garantizar que la unidad est libre de patgenos de la carga anterior (prevencin de contaminacin cruzada).
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Implcito en Numeral 4.a):] la limpieza del vehculo es parte de las #[b actividades previas al cargue] (donde se selecciona el animal).
              tr(data-aos="fade-right")
                td(data-label="Tcnica/requisito") #[b Secado y ventilacin (implcito)]
                td(data-label="Detalle y objetivo del procedimiento") Aunque no est textualmente detallada, una tcnica crtica posterior al lavado y desinfeccin es el #[b secado]. Los vehculos deben contar con ventilacin (Num. 4.f), lo cual facilita que las superficies queden secas antes del cargue, ya que la humedad residual puede neutralizar desinfectantes o favorecer el crecimiento bacteriano.
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 4.f):] exige sistemas para mantener la #[b ventilacin constante].
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de producto para aseo y desinfeccin
      
    p(data-aos="fade-right") La normativa consultada (Resolucin 20223040006915 y anexos) establece la obligacin de realizar el lavado y la desinfeccin del vehculo, pero no especifica la marca o el tipo qumico exacto de los productos a utilizar (detergentes, desinfectantes, desodorizantes, etc.).
    p.mb-4(data-aos="fade-right") El foco regulatorio se pone en el resultado de la limpieza (la prevencin de enfermedades), no en el insumo especfico.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de productos por propsito (generalidades)
    p.mb-4(data-aos="fade-right") Aunque el manual no los nombra, la tcnica de aseo requiere el uso de dos tipos de productos en secuencia:

    .row.justify-content-center.mb-4
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.color-primario.tarjeta.color-primarios2.p-4.h-100
          .row.justify-content-center.mb-3
            .col-lg-4.col-6.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/temas/tema2/img25.svg', alt='Texto que describa la imagen')
          .row.justify-content-center.mb-3
            .col-lg-8.p-2
              h5.mb-0.text-center Detergentes o Jabones
          p.text-center.mb-0 Productos usados para remover la suciedad gruesa y la materia orgnica (heces, orina, barro, sangre). Esta etapa es fundamental, ya que la materia orgnica inactiva la mayora de los desinfectantes.
      .col-lg-4(data-aos="fade-right") 
        .tarjeta.color-primario.tarjeta.color-primarios2.p-4.h-100
          .row.justify-content-center.mb-3
            .col-lg-4.col-6.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/temas/tema2/img26.svg', alt='Texto que describa la imagen')
          .row.justify-content-center.mb-3
            .col-lg-8.p-2
              h5.mb-0.text-center Desinfectantes
          p.text-center.mb-0 Productos qumicos (ej. yodo, amonios cuaternarios, formalina, hipoclorito) que se aplican despus de la limpieza para eliminar microorganismos (bacterias, virus, hongos) de las superficies.
      .col-lg-4(data-aos="fade-right")
        .tarjeta.h-100
          figure
            img(src='@/assets/curso/temas/tema2/img27.png', alt='Texto que describa la imagen')

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Regulacin de productos por el ICA
    p.mb-4(data-aos="fade-right") En el contexto de la bioseguridad en Colombia, la seleccin del desinfectante est implcitamente sujeta a las siguientes regulaciones sanitarias de uso peculiar:


    .tarjeta--container.row.mb-5
      .col-lg.tarjeta.color-primario.p-5
        .row.justify-content-center.mb-4
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/img28_3.svg', alt='Texto que describa la imagen')
        h5.text-center Fundamento
        p.mb-0 Los productos qumicos y desinfectantes utilizados en la cadena pecuaria deben estar registrados y aprobados por la autoridad sanitaria competente (ICA o Invima), garantizando que son seguros y efectivos para el propsito de bioseguridad en las granjas y vehculos de transporte.
      .col-lg.tarjeta.color-secundario.p-5
        .row.justify-content-center.mb-4
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/img29_3.svg', alt='Texto que describa la imagen')
        h5.text-center Requisito de cumplimiento
        p.mb-0 Los transportadores deben emplear productos que cumplan con la normativa sanitaria vigente para el control de patgenos en instalaciones y equipos pecuarios.
      .col-lg.tarjeta.color-acento-contenido.p-5
        .row.justify-content-center.mb-4
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/img30_3.svg', alt='Texto que describa la imagen')
        h5.text-center Soporte documental
        p.mb-0 La resolucin establece la necesidad de condiciones sanitarias y fitosanitarias, adems exige la desinfeccin, lo que implica el uso de productos aprobados.



    separador
    #t_2_7.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 2.7 Entidades de prevencin, vigilancia y control

    p(data-aos="fade-right") La vigilancia del bienestar animal y las condiciones sanitarias durante el transporte terrestre es una carga de autoridades especficas que tienen la facultad de inspeccionar, controlar y sancionar el incumplimiento de la normativa.
    p.mb-4(data-aos="fade-right") Por lo anterior, se invita a analizar la siguiente imagen:

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-left")
          h5 Figura 4.
          i  Vigilancia del animal en pie
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.movil(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema2/img30_1.png', alt='Figura 4 que relaciona un animal en pie (mula) junto a un estand del ICA que simboliza el cuidado que la entidad le brinda a estos animales.')
      .col-lg-10.desktop(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema2/img30.png', alt='Figura 4 que relaciona un animal en pie (mula) junto a un estand del ICA que simboliza el cuidado que la entidad le brinda a estos animales.')

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Concepto y marco de control
      
    p.mb-4(data-aos="fade-right") El control se define como la obligacin que tienen todos los involucrados en el transporte de permitir la revisin por parte de las autoridades competentes. Al respecto las siguientes entidades son las reguladoras:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 40.
      i Estndares de limpieza y aseo
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Entidad
                th Rol de vigilancia y control
                th Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td(data-label="Entidad") #[b Instituto Colombiano Agropecuario (ICA)]
                td(data-label="Rol de vigilancia y control") #[b Autoridad sanitaria y fitosanitaria] principal. Se encarga de la inspeccin y el control del cumplimiento de las condiciones de #[b bienestar animal] y los #[b protocolos sanitarios] durante la movilizacin.
                td(data-label="Soporte documental (texto exacto)") #[b Resolucin 20223040006915 (Art. 4, Pargrafo):] "Las personas naturales y/o jurdicas... estn en la obligacin de permitir la #[b inspeccin vigilancia y control], por parte de los funcionarios del #[b Instituto Colombiano Agropecuario - ICA], o quien este delegue o autorice".
              tr(data-aos="fade-right")
                td(data-label="Entidad") #[b Autoridades de Trnsito/MT]
                td(data-label="Rol de vigilancia y control") Autoridad de seguridad vial. Controlan el cumplimiento de la #[b capacidad de carga] (peso legal) y la tenencia del certificado de capacitacin en bienestar animal.
                td(data-label="Soporte documental (texto exacto)") #[b Manual de procedimiento (Num. 6):] El certificado de curso "...podr ser exigido por las #[b autoridades de trnsito y/o por el Instituto Colombiano Agropecuario - ICA]".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Clases de medidas y lineamientos de control
    p.mb-4(data-aos="fade-right") Las autoridades aplican medidas de vigilancia para corregir incumplimientos y prevenir riesgos sanitarios, encontrando:
    
    .row.mb-lg-5.mb-4
      .col-sm-10.col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta--boton.color-primario.tarjeta--BG04.p-4.h-100
          .row.justify-content-center.align-items-center.mb-3
            .col-12
              .divcentrado
                .imgg2.img1
          h5.text-center Medidas de prevencin	
          p.mb-0 Acciones destinadas a evitar que el riesgo se materialice, como exigir la reduccin de la densidad o el ajuste de la ventilacin en un punto de control ante una alerta de estrs trmico.
      .col-sm-10.col-lg-3.mb-4.mb-lg-0(data-aos="flip-right")
        .tarjeta--boton.color-primario.tarjeta--BG04.p-4.h-100
          .row.justify-content-center.align-items-center.mb-3
            .col-12
              .divcentrado
                .imgg2.img2
          h5.text-center Medidas de vigilancia
          p.mb-0 Monitoreo y seguimiento del cumplimiento de los protocolos sanitarios (ej. revisin de la limpieza y la estanqueidad del vehculo) e inspeccin de documentos (GSM y Certificado).
      .col-sm-10.col-lg-3.mb-4.mb-lg-0(data-aos="flip-left")
        .tarjeta--boton.color-primario.tarjeta--BG04.p-4.h-100
          .row.justify-content-center.align-items-center.mb-3
            .col-12
              .divcentrado
                .imgg2.img3
          h5.text-center Medidas de control (correctivas/sancionatorias)
          p.mb-0 Intervenciones directas como la inmovilizacin del vehculo o la aplicacin de sanciones (multas), ante el incumplimiento de la capacidad de carga o la evidencia de maltrato o riesgo sanitario inminente.
      .col-sm-10.col-lg-3.mb-4.mb-lg-0(data-aos="flip-left")
        .tarjeta--boton.color-primario.tarjeta--BG04.p-4.h-100
          .row.justify-content-center.align-items-center.mb-3
            .col-12
              .divcentrado
                .imgg2.img4
          h5.text-center Gua Sanitaria de Movilizacin (GSM)
          p.mb-0 Lineamiento de Clave de Control. Es el documento oficial del ICA que autoriza y acompaa el traslado, certificando la procedencia y el estado sanitario de la carga, siendo un requisito indispensable para la movilizacin legal.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Protocolos y clases de riesgos sanitarios

    p.mb-4(data-aos="fade-right") Los protocolos son las herramientas que las entidades controlan; los riesgos son lo que buscan evitar.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Protocolos sanitarios controlados
      
    p.mb-4(data-aos="fade-right") Los funcionarios de control se enfocan en verificar la correcta aplicacin de los protocolos de bioseguridad en los siguientes tipos:

    .row.justify-content-center.mb-5
      .col-lg-5.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta-avatar(style="padding-top: 0px !important;").h-100          
            .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
              img(src='@/assets/curso/temas/tema2/img19_1.png', alt='Imagen decorativa')
              .p-4
                h5 Protocolo de limpieza y desinfeccin
                p Se verifica que el vehculo est libre de residuos orgnicos de cargas anteriores y que el diseo estructural (superficies lisas, bordes redondeados) permita un aseo eficiente.
                p.mb-0 Soporte: la estructura debe permitir su "lavado y desinfeccin".
      .col-lg-5(data-aos="fade-right")
        .tarjeta-avatar(style="padding-top: 0px !important;").h-100            
            .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
              img(src='@/assets/curso/temas/tema2/img20_1.png', alt='Imagen decorativa')
              .p-4
                h5 Protocolo de contencin
                p Se revisa que el piso sea estanco para evitar que las excretas se filtren hacia la va pblica, previniendo la dispersin de patgenos.
                p.mb-0 Soporte: el piso debe contar con un sistema que "evite la filtracin de orina y heces hacia la va pblica".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Clases de riesgos sanitarios
      
    p.mb-4(data-aos="fade-right") Las entidades buscan identificar y mitigar los riesgos que afectan la salud animal, la salud pblica (inocuidad) y el medio ambiente, encontrando con ello las siguientes clases:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 41.
      i Acciones de los riesgos sanitarios
    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-right")
                th Clase de riesgo sanitario
                th Ejemplo de manifestacin
                th Consecuencia y control
            tbody
              tr(data-aos="fade-right")
                td(data-label="Clase de riesgo sanitario") #[b Riesgo biolgico (enfermedades)]
                td(data-label="Ejemplo de manifestacin") Contaminacin cruzada por #[b vehculo sucio] o movilizacin de un animal con #[b enfermedad de control oficial] sin notificacin.
                td(data-label="Consecuencia y control") La accin es la cuarentena o la disposicin sanitaria del vehculo y la carga.
              tr(data-aos="fade-right")
                td(data-label="Clase de riesgo sanitario") #[b Riesgo fsico (estructural/bienestar)]
                td(data-label="Ejemplo de manifestacin") Vehculo sin ventilacin, hacinamiento (por alta densidad de carga) o lesiones que puedan llevar a la muerte del animal.
                td(data-label="Consecuencia y control") El ICA puede exigir la descarga, la atencin inmediata o la redistribucin de la carga.
              tr(data-aos="fade-right")
                td(data-label="Clase de riesgo sanitario") #[b Riesgo ambiental]
                td(data-label="Ejemplo de manifestacin") #[b Filtracin de excretas] a la va pblica o fuentes de agua, lo que constituye un riesgo de contaminacin hdrica y diseminacin de patgenos.
                td(data-label="Consecuencia y control") La medida es la correccin inmediata del sistema de contencin.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de formatos de inspeccin y control
      
    p.mb-4(data-aos="fade-right") Los formatos especficos son documentos internos de las autoridades (ICA o trnsito), pero en la prctica, los principales instrumentos de control utilizados en carretera son:

    .row.justify-content-center.mb-4
      .col-12.movil.mb-4
        .tarjeta.tarjeta--BG04.p-4
          figure.mb-4
              img(src="@/assets/curso/temas/tema2/img37.png", alt="alt")
          h4.mb-3 Gua Sanitaria de Movilizacin (GSM)
          p.mb-0 Documento principal de control sanitario. Verifica la legalidad y el estado sanitario de la carga. El formato est estandarizado por el ICA.
      .col-xl-5.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0.desktop
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema2/img37.png", alt="alt")
          .crd_hover_txt--body
            .tit-tarj.p-3
              .ubicar
                .acordion__accion.acordion__accion__btn--b.h3.mb-0
                  i.fas.fa-angle-up
                h4.mb-3 Gua Sanitaria de Movilizacin (GSM)
            p.mt-3.px-4 Documento principal de control sanitario. Verifica la legalidad y el estado sanitario de la carga. El formato est estandarizado por el ICA.
